import Logger from 'frontend-logger';

const document = window.document,
      loaded   = {};

/**
 * @typedef {object} StyleLoadOptions
 * @param {String} fileName - file path to add
 * @param {String} key - a unique identifier
 * @param {String} suffix - a suffix to append to the file path
 */

/**
 * Handle the style load event.
 * @param {function} resolve - resolves the promise for the given stylesheet
 * @param {HTMLLinkElement} ss - the stylesheet which succeeded loading
 * @return {void}
 */
function onLoad ( resolve, ss ) {

	if ( ss.addEventListener ) {
		ss.removeEventListener( 'load', onLoad );
	}
	ss.media = 'all';

	resolve( ss );
}

/**
 * Handle load error.
 * @param {function} reject - rejects the promise for the given stylesheet
 * @param {HTMLLinkElement} ss - the stylesheet which failed loading
 * @return {void}
 */
function onError ( reject, ss ) {
	reject( ss );
}

/**
 * Check whether the given resource has already been loaded and load it when necessary by injecting a link tag in the document head.
 * @param {StyleLoadOptions} options - object defined below
 * @param {function} resolve - success callback for promised load handling
 * @param {function} reject - error callback for promised load handling
 * @return {void}
 */
function injectFile ( options, resolve, reject ) {

	const href = options.fileName + ( options.suffix !== null ?
	                                 '.' + options.suffix :
	                                 '' ) + '.css';

	if ( !loaded.hasOwnProperty( href ) ) {

		const ss   = document.createElement( 'link' ),
		      refs = document.getElementsByTagName( 'head' )[ 0 ].childNodes,
		      ref  = refs[ refs.length - 1 ];

		ss.rel   = 'stylesheet';
		ss.href  = href;
		ss.media = 'only x';
		ss.setAttribute( 'data-for', options.key );

		ss.addEventListener( 'load', onLoad.bind( this, resolve, ss ) );
		ss.addEventListener( 'error', onError.bind( this, reject, ss ) );

		ref.parentNode.insertBefore( ss, ref.nextSibling );
		loaded [ href ] = ss;

	} else {
		resolve( loaded [ href ] );
	}
}

/**
 * Load a file async and promisify the request.
 * @param {StyleLoadOptions} options - containing information about the file to load
 * @return {Promise} a promise which resolves on load
 */
function loadFile ( options ) {

	return new Promise( ( resolve, reject ) => {

		injectFile( options, resolve, reject );
	} );
}

/**
 * Wait for body to be defined to inject files.
 * @param {function} cb which will be executed after ready.
 * @return {void}
 */
function ready ( cb ) {

	if ( document.body ) {

		cb();
	} else {

		setTimeout( () => {
			ready( cb );
		} );
	}
}

const cache    = [],
      Injector = {

	      /**
	       * Add a file for batched processing on startup
	       * @param {String} file - file path to add
	       * @param {String} key - a unique identifier
	       * @param {String} suffix - a suffix to append to the file path
	       * @returns {void}
	       */
	      addFileForKeyToBatch( file, key = null, suffix = null ) {

		      cache.push( {
			                  fileName : file,
			                  suffix :   suffix,
			                  key :      key
		                  } );
	      },

	      /**
	       * Load a file immediately.
	       * @param {String} file - file path to add
	       * @param {String} key - a unique identifier
	       * @param {String} suffix - a suffix to append to the file path
	       * @return {Promise} a promise which resolves as soon as the file has been loaded.
	       */
	      injectImmediate( file, key = null, suffix = null ) {

		      return loadFile( {
			                       fileName : file,
			                       suffix :   suffix,
			                       key :      key
		                       } );
	      },

	      /**
	       * Start the service. Processes internal queue asap.
	       * @return {Promise} a promise which resolves as soon as the queue has been processed.
	       */
	      start() {

		      return new Promise( ( resolve, reject ) => {

			      ready( () => Promise.all( cache.map( loadFile ) )

					      .then( ( resolved ) => {

						      resolve( resolved );
					      }, ( rejected ) => {

						      Logger.warn( 'Stylesheet failed to load:', rejected.href );
						      reject( rejected );
					      } ) );
		      } );
	      }
      };

export default Injector;



