import UserAgent from 'express-useragent';
const userAgent = window.UserAgent = UserAgent.parse( navigator.userAgent );

/**
 *
 * @param {HTMLElement} root -  root node, html
 * @param {string} className - class to be added to the html element
 * @return {void}
 */
function setClass ( root, className ) {

	root.classList.add( className );
}

/**
 * Determine the users device.
 * @param {HTMLElement} html - the html root tag
 * @return {void}
 */
function determineDevice ( html ) {

	if ( userAgent.isMobile ) {
		setClass( html, 'is-mobile' );
	}

	if ( userAgent.isAndroid || userAgent.isAndroidTablet ) {
		setClass( html, 'is-android' );
	}

	if ( userAgent.isiPhone || userAgent.isiPad || userAgent.isiPod ) {
		setClass( html, 'is-ios' );
	}

	if ( userAgent.isDesktop ) {
		setClass( html, 'is-desktop' );
	}

	setClass( html, 'is-' + userAgent.browser.toLowerCase() );
}

export default determineDevice;



