let root;

/**
 *
 * @param {string} className - class to be added to the html element
 * @return {void}
 */
function setClass ( className ) {
	root.classList.add( className );
}

/**
 * Wats until element is available and executes a callback function
 * @param  {Function} fn Callback to be executed
 * @return {void}
 */
function checkExecute ( fn ) {
	root = document.getElementById( 'main-header' );
	if ( !root ) {
		setTimeout( checkExecute.bind( null, fn ), 0 );
		return;
	}

	fn();
}

/**
 * Determine the users device.
 * @param {HTMLElement} html - the html root tag
 * @return {void}
 */
function init () {
	checkExecute( setClass.bind( null, 'main-header--ready' ) );
}

export default init;



