(function setObjectIDFunctionality () {
	if ( typeof Object._uid === 'undefined' ) {
		let id = 0;

		Object._uid = ( o ) => {
			if ( typeof o.__uniqueid === 'undefined' ) {
				Object.defineProperty( o, '__uniqueid', {
					value :      ++id,
					enumerable : false,
					writable :   false
				} );
			}

			return o.__uniqueid;
		};
	}
})();



