/**
 * Set a cookie value
 * @param {string} name - identifies the cookie
 * @param {string} value - the value
 * @param {int} expirationDays - expiration in days
 * @returns {void}
 */
function setCookie ( name, value, expirationDays = 365 ) {
	const d       = new Date( Date.now() + expirationDays * 24 * 60 * 60 * 1000 ),
	      expires = '; expires=' + d.toString(),
	      path    = '; path=/';

	document.cookie = name + '=' + value + expires + path;
}

/**
 * Get a cookie value by name.
 * @param {string} cookieName - an identifier
 * @return {boolean|string} the value or false if nothing was set.
 */
function getCookie ( cookieName ) {
	const name = cookieName + '=',
	      ca   = document.cookie.split( ';' );

	for ( let i = 0; i < ca.length; i++ ) {
		let c = ca[ i ];

		while ( c.charAt( 0 ) === ' ' ) {
			c = c.substring( 1 );
		}
		if ( c.indexOf( name ) !== -1 ) {
			return c.substring( name.length, c.length );
		}
	}
	return false;
}

function deleteCookie ( name ) {
	document.cookie = name + '=; expires=Thu, 01 Jan 1970 00:00:01 GMT;';
}

const CookieService = {
	setCookie,
	getCookie,
	deleteCookie
};

export default CookieService;



