const undefinedType = { type : 'undefined' };

const tryParse = ( data ) => {

    try {
        return JSON.parse( data );
    } catch (e) {

        return undefinedType;
    }
};

/**
 *
 * @param {Object} event - received message event
 * @returns {Object} data which was sent by the event, falls back to an empty object if there was an event without data passed
 */
export default ( event ) => event.data ? tryParse( event.data ) : undefinedType;



