import FontFaceObserver from 'fontfaceobserver';
import CookieService from './../services/Cookie.service.js';
import Logger from 'frontend-logger';
import Fonts from './../../config/styles/fonts.json';

const

      exLight           = new FontFaceObserver( Fonts.font.faces.exlight ),
      light             = new FontFaceObserver( Fonts.font.faces.light ),
      medium            = new FontFaceObserver( Fonts.font.faces.medium ),
      isCached          = 'is-cached',

      FontLoadedClasses = {
	      exLight :          'font-ex-light-loaded',
	      light :            'font-light-loaded',
	      medium :           'font-medium-loaded',
	      all :              'fonts-all-loaded',
	      failed :           'fonts-all-failed',
	      loading :          'fonts-loading',
	      loadingAnimation : 'fonts-loading--animation'
      };

let root;

/**
 * Set a class on the root element.
 * @param {string} className - class to be added to the html element
 * @return {void}
 */
function setClass ( className ) {

	root.classList.add( className );
}

/**
 * Remove a class on the root element.
 * @param {string} className - class to be added to the html element
 * @return {void}
 */
function removeClass ( className ) {

	root.classList.remove( className );
}

/**
 * Set a class and store the operation in a cookie.
 * @param {string} className which should be persisted and set
 * @return {void}
 */
function setClassAndMemoize ( className ) {

	setClass( className );
	CookieService.setCookie( className, isCached );
}

/**
 * Create a timeout promise to race against.
 * @return {Promise<T>|Promise} will be rejected after 1500ms.
 */
function fontTimer () {
	return new Promise( ( resolve, reject ) => {

		setTimeout( reject, 1500 );
	} );
}

//todo remove and move to a global window service.
/**
 * Create a window.onload promise.
 * @return {Promise<T>|Promise} will be resolved on window load.
 */
function loaded () {
	return new Promise( ( resolve, reject ) => {

		window.addEventListener( 'load', resolve );
	} );
}

/**
 * Wait for all fonts to be loaded and set the respective loaded classes.
 * Additionally set a class when all fonts are succesfully loaded.
 * In case of failure set an error class.
 * @return {void}
 */
function setClassesOnReady () {

	exLight.load()
			.then( () => setClass( FontLoadedClasses.exLight ) )
			.catch( () => {
				Logger.warn( 'ExLight font family could not be loaded' );
			} );

	light.load()
			.then( () => setClass( FontLoadedClasses.light ) )
			.catch( () => {
				Logger.warn( 'Light font family could not be loaded' );
			} );

	medium.load()
			.then( () => setClass( FontLoadedClasses.medium ) )
			.catch( () => {
				Logger.warn( 'Medium font family could not be loaded' );
			} );

	Promise
			.race( [
				       fontTimer(),
				       Promise.all( [ exLight, light, medium ] )
			       ] )

			.then( () => {
				setClassAndMemoize( FontLoadedClasses.all );
				removeClass( FontLoadedClasses.loading );
			} )
			.catch( () => {
				setClass( FontLoadedClasses.failed );
				removeClass( FontLoadedClasses.loading );
			} );
}

/**
 * Set all ready classes now.
 * @return {void}
 */
function setClassesImmediate () {

	setClass( FontLoadedClasses.exLight );
	setClass( FontLoadedClasses.light );
	setClass( FontLoadedClasses.medium );
	setClass( FontLoadedClasses.all );
	removeClass( FontLoadedClasses.loading );
}

/**
 * Remove animation classes after window loaded, to avoid style overrides
 * @return {void}
 */
function removeClassesOnLoad () {

	loaded()
			.then( () => removeClass( FontLoadedClasses.loadingAnimation ) )
			.catch( () => removeClass( FontLoadedClasses.loadingAnimation ) );
}

/**
 * Observe the loading of fonts.
 * @param {HTMLElement} scope - the root node of the application.
 * @return {void}
 */
function init ( scope ) {

	root = scope;

	if ( !CookieService.getCookie( FontLoadedClasses.all ) ) {

		setClassesOnReady();
	} else {

		setClassesImmediate();
	}

	removeClassesOnLoad();
}

export default init;



