import './../compatibility/RelPreload.polyfill';
import StyleInjector from './../../../local-packages/services/style-injector/StyleInjector.service';

const BreakpointConfig = require( './../../config/styles/breakpoints.json' ).breakpoints,
      BASE_PATH        = GLOBAL_CONFIG.CSS_PATH;

function loadBreakpointStyles ( filePath ) {
	const width   = Math.max( window.innerWidth, window.outerWidth ),
	      allowed = [ 'md', 'lg', 'xl', 'xxl' ];

	StyleInjector.addFileForKeyToBatch(filePath, BreakpointConfig.sm.name);

	allowed.forEach( ( key ) => {

		if ( BreakpointConfig.hasOwnProperty( key ) ) {
			const breakpoint = BreakpointConfig[ key ];

			if ( width >= breakpoint.min ) {

				StyleInjector.addFileForKeyToBatch(filePath, breakpoint.name, breakpoint.name);
			}
		}
	} );

	StyleInjector.start()
			.then(() => {})
			.catch(() => {});
}

/**
 * Loads stylesheets for the current breakpoint asynchronously.
 * @param {string|array} filename - filename(s) of stylesheets which has to be loaded
 * @return {void}
 */
function loadStyles ( filename ) {

	let fnames;

	if ( typeof filename === 'string' ) {
		fnames = filename.split( ',' );
	} else if ( Object.prototype.toString.call( filename ) !== '[object Array]' ) {
		fnames = [];
	}

	fnames.reverse().forEach( ( fname ) => {

		loadBreakpointStyles( BASE_PATH + fname.trim() );
	} );
}

export default loadStyles;



