import './compatibility/Compatibility.lib';

import getData from './helper/PostMessage.helper';
import fontObserve from './runtime/FontOberserver.runtime';
import stylesheets from './runtime/Stylesheet.runtime';
import userAgent from './runtime/UserAgent.runtime';
import mainHeader from './runtime/MainHeader.runtime';
import bucket from './services/Bucket.service';
import './helper/UID.helper';

const
    html = document.getElementsByTagName( 'html' )[0];

(() => {
    fontObserve( html );
    userAgent( html );
    mainHeader();
    stylesheets( 'screen' );

    window.VIDEOJS_NO_DYNAMIC_STYLE = true;

    window.addEventListener( 'message', ( e ) => {
        const data = getData( e );

        if (data.type === 'formReady') {
            bucket.formReady.add( data.data.id );
        }
    } );

})();



