class Bucket {

	constructor () {

		this.win = window;
		this.doc = document;
		this.$window   = null;
		this.$document = null;

		this.html = document.getElementsByTagName( 'html' )[ 0 ];
		this.body = null;
		this.$html = null;
		this.$body = null;

		this.isLoaded = false;
		this.hasStage = false;

		this.formReady = new Set();

		this.controller = {
			framework : {}
		};
	}

	/**
	 * @abstract
	 * @param {jQuery} jQuery - instance
	 * @returns {AbstractController} - instance
	 */
	init ( jQuery ) {
		this.$window   = jQuery( this.win );
		this.$document = jQuery( this.doc );

		this.body     = document.getElementsByTagName( 'body' )[ 0 ];
		this.hasStage = this.body.classList.contains( 'has-stage' );

		this.$html     = jQuery( this.html );
		this.$body     = jQuery( this.body );
	}
}

const instance = new Bucket();

export default instance;



